/* eslint-disable max-len */
import React, { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import HorizontalSearchHeader from 'components/layouts/HorizontalSearchHeader';
import Listitemgarage from 'components/layouts/Listitemgarage';
import { Pagination } from 'react-headless-pagination';
import { useHistory } from 'react-router-dom';
import { getGarages } from 'services/axios';

function garagelist() {
  const [garages, setGarages] = useState([]);
  const [currentPage, setCurrentPage] = useState(0);
  const [totalPages, setTotalPages] = useState(0);

  useEffect(() => {
    getGarages(0).then((res) => {
      setGarages(res.data?.results.pageData);
      setCurrentPage(res.data?.results.currentPage);
      setTotalPages(res.data?.results.totalPages);
    })
      .catch((err) => {
        console.log('err', err);
      });
  }, []);
  function clickNext() {
    if (currentPage + 1 < totalPages) {
      getGarages(currentPage + 1).then((res) => {
        setGarages(res.data?.results.pageData);
        setCurrentPage(currentPage + 1);
      })
        .catch((err) => {
          console.log('err', err);
        });
    }
  }
  function clickPrevious() {
    if (currentPage - 1 >= 0) {
      getGarages(currentPage - 1).then((res) => {
        setGarages(res.data?.results.pageData);
        setCurrentPage(currentPage - 1);
      })
        .catch((err) => {
          console.log('err', err);
        });
    }
  }
  const history = useHistory();
  return (
    <>
      <Helmet title="Garages" />
      <div className="absolute right-20 my-3.5" style={{ fontFamily: 'Quicksand' }}>
        <div
          onClick={() => history.push('/garage/addgarage/-1')}
          style={{
            borderRadius: '4px', fontWeight: '500', backgroundColor: '#013453', color: '#FFFFFF', fontSize: '16px', width: '205px', height: '52px', boxShadow: '0px 8px 16px #005B923D', padding: '13px 30px', textDecoration: 'none', cursor: 'pointer',
          }}
        >
          Add New Garage +
        </div>
      </div>
      <div>
        <div className="flex flex-col space-y-2">
          <div className="space-y-2 basic-1/2 mx-5">
            <span className="font-quicksand-semi-bold text-4xl mr-3.5">
              Garages
            </span>
            <HorizontalSearchHeader Title="All Garages" />
          </div>

        </div>
        <div className="box-border mb-px">
          <div className="flex flex-row flex-nowrap mx-5">
            <h1 className="text-base font-quicksand-medium font-medium basis-1/3 bg-white p-4 mr-1">Garage Name</h1>
            {/* <h1 className="text-base font-quicksand-medium font-medium basis-1/3 bg-white p-4 mr-1">Garage Series</h1> */}
            <h1 className="text-base font-quicksand-medium font-medium basis-1/3 bg-white p-4 mr-1">Status</h1>
            <h1 className="text-base font-quicksand-medium font-medium basis-1/3 bg-white p-4 mr-1">Action</h1>
          </div>

        </div>
        <div>
          {garages.map((item) => (
            <Listitemgarage
              garage_id={item.id}
              garage_name={item.name}
              garage_members={item.garage_series}
              status={String(item.isActive)}
            />
          ))}
        </div>
        <Pagination
          currentPage={currentPage}
          totalPages={totalPages}
          edgePageCount={2}
          middlePagesSiblingCount={2}
          className=""
          truncableText="..."
          truncableClassName="mt-5 mx-5"
        >
          <div className="flex items-center justify-center flex-grow mt-5">
            <div onClick={clickPrevious}>
              <Pagination.PrevButton className="mt-5 bg-white mx-5 rounded">{'<'}</Pagination.PrevButton>
            </div>
            <Pagination.PageButton
              activeClassName="bg-teal-400 text-white"
              inactiveClassName="bg-white"
              className="p-3 mx-4 mt-5 rounded"
            />
            <div onClick={clickNext}>
              <Pagination.NextButton className="mt-5 bg-white mx-5 rounded">{'>'}</Pagination.NextButton>
            </div>
          </div>
        </Pagination>
        {/* <Items currentItems={currentItems} /> */}
      </div>
    </>
  );
}

export default garagelist;
